.highlight-section .container a {
  color: #ffffff !important;
  text-decoration: underline;
}
.btn, .card .field--name-field-featured-link {
  text-decoration: none !important;
}
h2 {
  font-family: "Montserrat";
  font-weight: 700;
  margin-top: 10px;
}
h3 {
  font-weight: 600;
}
.superintendent-search-menu h2,
.h2-layout,
.menu-header {
  color: #000;
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 15px;
  margin-top: 0;
  padding: 0 0 0 20px;
  position: relative;
  text-transform: uppercase;
  &:after {
    background-color: #ed1c24;
    bottom: 0;
    content: "";
    left: 0;
    position: absolute;
    top: 0;
    width: 5px;
  }
}
.ns-heading {
  display: block;
  font-family: "Montserrat";
  font-size: 1.5em;
  font-weight: 800;
  margin-bottom: 20px;
  margin-top: 0;
  position: relative;
  text-transform: uppercase;
  z-index: 1;
}
.line-after-heading:before {
  background-color: #fff;
  bottom: -2rem;
  content: "";
  display: block;
  height: 2px;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  right: 0;
  width: 4rem;
  z-index: 1;
}
.line-after-heading-dark:before {
  background-color: #000;
  bottom: -1rem;
  content: "";
  display: block;
  height: 2px;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  right: 0;
  width: 4rem;
  z-index: 1;
}
.region-content {
  line-height: 2em;
  h2,
  h3,
  h4 {
    margin-top: 20px;
  }
}
.region-featured-pages,
.region-top-stories,
.region-featured-video,
.district-calendar,
.district-events,
.content-section,
.region-content {
  a {
    color: $link-color;
  }
  a:active {
    color: $link-color;
  }
  a:focus {
    background-color: #f1f1f1;
    border-color: $link-color-lighter;
    box-shadow: 0 0 10px $link-color-lighter;
    color: $link-color;
    outline: none;
  }
  a:visited {
    color: $link-color-darker;
  }
}

/**
 * Hover effect for Nav, Footer, etc.
 */

.dropdown-toggle .dropdown-menu li > a:before,
.footer li > a:before,
.navbar-nav li > a:before,
#block-superintendentsearchmenu a:before,
.view-department-pages-menu a:before,
.views-field.views-field-field-link-to-resource a::before {
  -webkit-transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  bottom: -4px;
  content: "";
  height: 3px;
  left: 0;
  position: absolute;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
  visibility: hidden;
  width: 100%;
}
.dropdown-toggle .dropdown-menu li > a:hover:before,
.calendar__info h3 > a:hover:before,
.event__info li > h3 > a:hover:before,
.footer li > a:hover:before,
.navbar-nav li > a:hover:before,
#block-superintendentsearchmenu a:hover:before,
.view-department-pages-menu a:hover:before,
.views-field.views-field-field-link-to-resource a:hover:before {
  -webkit-transform: scaleX(1);
  background-color: $brand-primary;
  transform: scaleX(1);
  visibility: visible;
}
.dropdown-toggle .dropdown-menu li > a,
.calendar__info a,
.footer li > a,
.navbar-nav li > a {
  display: inline-block;
  text-decoration: none;
}

/* Hover effect for content areas */

.calendar__item a,
.gsc-cursor-page,
.card a,
.card__list a,
.region-content a,
.view-article-tags a {
  display: inline-block;
  text-decoration: none;
}
.card a,
.card__list a,
.list_card__inner a {
  display: inline-block;
}
.calendar__item a::after,
.gsc-cursor-page::after,
.card__list a::after,
.list_card__inner a::after,
.region-content a::after,
.view-article-tags a::after,
a.content-link::after {
  background: $brand-primary;
  content: '';
  display: block;
  height: 3px;
  transition: width 0.3s;
  width: 0;
}
.calendar__item a:hover::after,
.gsc-cursor-page:hover::after,
.card a:hover::after,
.card__list a:hover::after,
.list_card__inner a:hover::after,
.region-content a:hover::after,
.view-article-tags a:hover::after {
  width: 100%;
}
