/* Remove underline when a link is styled as a button */

.region-content a.btn {
  margin-top: 15px;
}
.region-content a.btn::after,
.region-content .btn-primary a::after  {
  height: 0;
}
.card a.btn:hover::after,
.card__list a.btn:hover::after,
.list_card__inner a.btn:hover::after,
.region-content a.btn:hover::after,
.view-article-tags a.btn:hover::after {
  width: 100%;
}
.fa-angle-double-right:before {
  color: $link-color;
}

/*Focus style for buttons */

.card a.btn:focus,
.card__list a.btn:focus {
  background: #222222;
  border-color: #222222;
  box-shadow: 0 0 10px $link-color-lighter;
  color: #ffffff;
  outline: none;
  transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
  -webkit-transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
}

/* Hover effect with arrow */

#block-superintendentsearchmenu a,
#block-superintendentsearchmenu a:hover,
#block-superintendentsearchmenu a:visited,
.view-department-pages-menu a,
.view-department-pages-menu a:hover,
.view-department-pages-menu a:visited,
.views-field.views-field-field-link-to-resource a,
.views-field.views-field-field-link-to-resource a:hover,
.views-field.views-field-field-link-to-resource a:visited {
  border-bottom: 1px solid #eee;
  display: block;
  padding: 15px 0;
  position: relative;
}
#block-superintendentsearchmenu a:focus,
.view-department-pages-menu a:focus,
.views-field.views-field-field-link-to-resource a:focus {
  border-color: $link-color-lighter;
  box-shadow: 0 0 10px $link-color-lighter;
  outline: none;
}
.card__list {
  a {
    border-bottom: 0;
    display: inline-block;
    padding: 10px 0;
  }
  li {
    clear: both;
    line-height: 1.5em;
    list-style: none;
  }
}
#block-superintendentsearchmenu a.current:hover:after,
.view-department-pages-menu a.current:hover:after,
.views-field.views-field-field-link-to-resource a:hover:after {
  animation: none;
}
#block-superintendentsearchmenu a:hover,
.view-department-pages-menu a:hover,
.views-field.views-field-field-link-to-resource a:hover {
  text-decoration: none;
}
#block-superintendentsearchmenu,
.view-department-pages-menu,
.views-field.views-field-field-link-to-resource {
  a:after {
    content: '\f105';
    font-family: "Font Awesome 5 Free";
    font-size: 1em;
    font-size: 20px;
    font-weight: 600;
    margin-left: 7px;
    position: relative;
    top: 3px;
    transition: all 500ms ease;
  }
}
#block-superintendentsearchmenu a:hover:after,
.view-department-pages-menu a:hover:after,
.views-field.views-field-field-link-to-resource a:hover:after {
  margin-left: 15px;
}
