}
.desktop-ad-space {
  float: left;
  height: 250px;
  padding: 0;
  width: 300px;
}
.ckeditor-accordion-container > dl dt.active > a,
.ckeditor-accordion-container > dl dt > a,
.ckeditor-accordion-container > dl dt > a:active,
.ckeditor-accordion-container > dl dt > a:focus,
.ckeditor-accordion-container > dl dt > a:visited {
  background-color: $link-color;
  color: $white;
}
.ckeditor-accordion-container > dl dt.active,
.ckeditor-accordion-container > dl dt.active > a {
  background-color: #00a5ef;
  color: $white;
}

.ckeditor-accordion-container > dl {
  border: 1px solid $link-color;
}
.ckeditor-accordion-container > dl table {
  border-top: 0;
}
a {
  color: $link-color;
  text-decoration: none;
  &:hover,
  &:focus {
    color: $link-color-darker;
  }
}
.main-footer a {
  color: #fff;
  text-decoration: none;
  &:hover,
  &:focus {
    color: #fff;
  }
}
.card__list ul {
  padding-left: 0;
}
#superintendent-search-page-menu {
  display: none;
}
.superintendent-search-menu li > a {
  margin: 0 !important;
}
.superintendent-search-menu li > a:hover,
.superintendent-search-menu li > a:focus {
  background-color: transparent !important;
}
.block-views-blockdepartments-listing-block-1 {
  margin-bottom: 0 !important;
}
#block-schoolboardandsuperintendent {
  margin-bottom: 80px;
}
#block-schoolboardandsuperintendent a:focus {
  background-color: #f1f1f1;
  color: $link-color;
  outline: none;
  border-color: $link-color-lighter;
  box-shadow: 0 0 10px $link-color-lighter;
}
.region-home-slideshow {
  //margin-bottom: 80px;
  margin-bottom: 60px;
}
.field--type-image img {
  margin: 0 auto;
}
.ad_frame {
  border: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-height: 100%;
  max-width: 100%;
  min-height: 250px;
  min-width: 300px;
