button.button.js-form-submit.form-submit.btn-primary.btn {
  background-color: $link-color;
  border-color: $link-color;
  border-style: solid;
  border-width: 0;
  line-height: normal;
  padding: 7px 7px 8px 7px !important;
}
button.button.js-form-submit.form-submit.btn-primary.btn:hover {
  background: #222222;
  border-color: #222222;
  color: #ffffff;
  outline: none;
  transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
  -webkit-transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
}
button.button.js-form-submit.form-submit.btn-primary.btn:focus {
  background: #222222;
  border-color: #222222;
  box-shadow: 0 0 10px $link-color-lighter;
  color: #ffffff;
  outline: none;
  transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
  -webkit-transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
}
ul.list-inline.article-date {
  font-size: 13px;
}
.article_item {
  margin-bottom: 35px;
}
.feed-icons {
