}
#block-exposedformschools-listingpage-1 {
  margin-bottom: 25px;
}
.article .field--name-body {
  margin: 50px 0;
}
.panel:last-child,
p:last-child {
  margin-bottom: 0;
}
#block-bootstrap-subtheme-search {
  display: none;
}
.region-ad-space {
  margin-top: 43px;
  margin-left: 0;
  padding: 0;
  width: 300px;
  img {
    width: 100%;
  }
}
.form-group {
  margin: 0;
}
.content-section {
  margin-bottom: 80px;
}
.content-section-w {
  background: #fff;
  margin-bottom: 80px;
  padding: 80px 0;
}
.btn {
  border-bottom: none !important;
  border-radius: 2px;
  border: none;
  color: #fff !important;
  cursor: pointer;
  display: inline-block;
  font-weight: 400 !important;
  line-height: 15px;
  padding: 15px !important;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
  a.btn:focus,
  .slide__link a:focus,
  #block-searchform button,
  &:active,
  &:hover {
    background: #222222;
    border-color: #222222;
    color: #ffffff;
    transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
    -webkit-transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
  }
  &:focus {
    background: #222222;
    border-color: #222222;
    box-shadow: 0 0 10px $link-color-lighter;
    color: #ffffff;
    outline: none;
    transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
    -webkit-transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
  }
}
.btn-primary,
.btn-primary:focus {
  background-color: $link-color;
  border-color: $link-color;
}
.btn-primary a {
  color: white;
  text-decoration: none;
}
.card .field--name-field-featured-link {
  @extend .btn;
  @extend .btn-primary;
}
ol,
ul {
  padding-left: 1.5em;
  .popover &:last-child {
    margin-bottom: 0;
  }
}

// Help region

.region-help {
  > .glyphicon {
    float: left;
    font-size: $font-size-large;
    margin: -0.05em 0.5em 0 0;
  }
  .block {
    overflow: hidden;
  }
}
.control-group .help-inline,
.help-block {
  color: $gray-light;
  font-size: 12px;
  margin: 5px 0 10px;
  padding: 0;
  &:first-child {
    margin-top: 0;
  }
}
.filter-btn {
  display: none;
}
.m-dpt-nav {
  display: none;
}
.region.region-department-sidebar-first {
  display: block;
}

// Tablet styles < 1200px

@media screen and (max-width: 1200px) {
  .list-cards {
    margin-left: 0;
  }
}
@media screen and (max-width: 996px) {
  .desktop-ad-space {
    clear: both;
    float: none;
    margin: 0 auto;
    padding-top: 30px;
  }
  .region-ad-space {
    margin: 0;
    padding: 0;
    width: 300px;
    img {
      width: 100%;
    }
  }
  .ad_frame {
    min-height: 248px;
  }
  .m-dpt-nav {
    background-color: $brand-primary;
    display: block;
    margin-bottom: 25px;
    padding: 15px;
    width: 100%;
  }
  .m-dpt-nav__label {
    background-color: #348CA2;
    border-radius: 0;
    color: #fff;
    display: inline;
    font-weight: bold;
    list-style: none;
    margin-bottom: 0;
    margin-top: 0;
    padding: 15px 25px;
  }
  .breadcrumb-container {
    display: none;
  }
  .region.region-department-sidebar-first {
    display: none;
    margin-top: 20px;
    // For Safari 3.1 to 6.0
    transition: width 2s;
    -webkit-transition: width 2s;
  }
  .content-area {
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .region-filters {
    margin-bottom: 25px;
  }
  .region-filters .views-exposed-form {
    display: none;
  }
  .filter-btn {
    display: block;
  }
}

// Phone styles < 767px

@media screen and (max-width: 767px) {
  body .userway.userway_p3,
  body .userway.userway_p4,
  body .userway.userway_p4.userway_hidden.userway_over,
  body .userway.userway_p5 {
    bottom: 46px !important;
  }
  body .userway.userway-the-big.userway_p3,
  body .userway.userway-the-big.userway_p4,
  body .userway.userway-the-big.userway_p4.userway_hidden.userway_over,
  body .userway.userway-the-big.userway_p5 {
    bottom: 66px !important;
  }
  .department-header img {
    width: auto;
  }
  .img-responsive {
    max-width: none;
  }
  #superintendent-search-page-menu {
    display: block;
  }
  .top-story {
    min-height: auto;
  }
  .featured-link {
    min-height: auto;
  }
  .view-footer {
    clear: both;
    display: block;
  }
  #edit-submit-schools-listing {
    clear: both;
    display: block;
  }
  .main-footer {
    padding: 15px;
  }
  .main-footer nav {
    margin-bottom: 25px;
  }
  .highlight-section {
    padding: 40px 15px;
  }
  #block-financialtransparency {
    margin-bottom: 25px;
    text-align: center;
  }
  .btn {
    font-size: 11px;
    line-height: 10px;
    padding: 10px !important;
  }
  .region-home-slideshow {
    margin-bottom: 50px;
  }
  .block-views-blockdepartments-listing-block-1 {
    margin-bottom: 0 !important;
  }
  #block-schoolboardandsuperintendent {
    margin-bottom: 50px;
  }
  .content-section {
    margin-bottom: 50px;
  }
  .content-section-w {
    background: #fff;
    margin-bottom: 50px;
    padding: 50px 0;
  }
  .district-events {
    margin-top: 100px;
  }
  .region-search-area {
    left: 50%;
    margin-left: -50vw;
    position: relative;
    width: 100vw;
  }
  .spectar {
    display: none;
  }
  .region-ad-space {
    display: none;
  }
  .ad_frame_mobile {
    border: 0;
    height: 45px;
    max-height: 45px;
  }
  .navbar-inverse .navbar-nav > .open > a,
  .navbar-inverse .navbar-nav > .open > a:hover,
  .navbar-inverse .navbar-nav > .open > a:focus {
    background-color: transparent;
  }
  .card.article_item {
    min-height: auto;
  }
  .top-story .card__content .card__title,
  .article_item .card__content .card__title {
    min-height: auto;
  }
}
