.main-footer {
  background-repeat: no-repeat;
  background-size: cover;
  background: url("https://images.pexels.com/photos/267885/pexels-photo-267885.jpeg");
  padding: 60px;
  position: relative;
  &:before {
    @include dark-overlay(rgba(34,34,34, 0.9));
    content: "";
  }
  li a:hover {
    background-color: transparent;
  }
}
.footer-menu li a {
  margin-left: 0;
}
.footer-copyright {
  background-color: #222;
  color: #9d9d9d;
  padding: 25px 0;
  position: relative;
}
.footer {
  border-top: 1px solid #e5e5e5;
  padding: 0 0 45px 0;
  h2,
  p {
    color: #fff;
  }
  a {
    color: $link-color-light;
  }
  a:focus {
    border-color: $link-color-lighter;
    box-shadow: 0 0 10px $link-color-lighter;
    color: $link-color-light;
    outline: none;
  }
  a:hover {
    color: $link-color-light;
  }
  a:visited {
    color: $link-color-light-darker;
  }
  h2 {
    color: #fff;
    font-size: 15px;
    font-weight: bold;
    padding: 0 0 0 20px;
    position: relative;
    text-transform: uppercase;
    &:after {
      background-color: $link-color;
      bottom: 0;
      content: "";
      left: 0;
      position: absolute;
      top: 0;
      width: 5px;
    }
  }
}
#transparency-florida-link {
  display: inline-block;
}
