/* Navbar */

$mobile: "screen and (max-width: #{$screen-xs-max})";
$tablet: "screen and (min-width: #{$screen-sm-min})";
$normal: "screen and (min-width: #{$screen-md-min})";
$wide: "screen and (min-width: #{$screen-lg-min})";
$grid-breakpoint: "screen and (min-width: #{$grid-float-breakpoint})";
.navbar-inverse .navbar-nav > .active > a,
.navbar-inverse .navbar-nav > .active > a:focus,
.navbar-inverse .navbar-nav > .active > a:hover {
  background-color: transparent;
}
.navbar-inverse .navbar-toggle .icon-bar {
  background-color: #fff;
}
nav {
  position: relative;
}
.navbar-inverse .menu--main > .active > a,
.navbar-inverse .menu--main > .active > a:active,
.navbar-inverse .menu--main > .active > a:link,
.navbar-inverse .menu--main > li > a,
.navbar-inverse .menu--main > li > a:active,
.navbar-inverse .menu--main > li > a:link {
  background-color: transparent;
  color: #000 !important;
}
body {
  // Fix horizontal scrolling on iOS devices: @see https://www.drupal.org/node/1870076
  position: relative;
  &.navbar-is-static-top {
    margin-top: 0;
  }
  &.navbar-is-fixed-top {
    margin-top: ($navbar-height + ($grid-gutter-width / 2));
  }
  &.navbar-is-fixed-bottom {
    padding-bottom: ($navbar-height + ($grid-gutter-width / 2));
  }
  @media #{$tablet} {
    margin-top: ($grid-gutter-width / 2);
  }
  @media #{$mobile} {
    &.toolbar-vertical {
      &.navbar-is-fixed-bottom,
      &.navbar-is-fixed-top {
        .toolbar-bar {
          position: fixed;
        }
        header {
          z-index: 500;
        }
      }
      // Default toolbar fixed height value: @see core/modules/toolbar/css/toolbar.icons.theme.css@261
      &.navbar-is-fixed-top header {
        top: 39px;
      }
    }
  }
}
.navbar.container {
  @media #{$tablet} {
    max-width: (($container-sm - $grid-gutter-width));
  }
  @media #{$normal} {
    max-width: (($container-md - $grid-gutter-width));
  }
  @media #{$wide} {
    max-width: (($container-lg - $grid-gutter-width));
  }
}
#navbar {
  border: 0;
  text-transform: uppercase;
}
.main-header {
  background-color: #fff;
  height: 200px !important; // Need to use the important tag to overwrite Bootstrap's height:auto !important.
}
#navbar-collapse {
  overflow: hidden;
}
.navbar .logo {
  margin-right: -($grid-gutter-width / 2);
  padding-left: ($grid-gutter-width / 2);
  padding-right: ($grid-gutter-width / 2);
  @media #{$grid-breakpoint} {
    margin-right: 0;
    padding-left: 0;
  }
}
.navbar-header {
  float: none !important;
}
.spectar {
  background: rgba(0, 0, 0, 0) linear-gradient(to right, #49c587 0%, #49c587 1%, #4bc9e2 6%, #ff7575 13%, #f0c851 20%, #8d74b9 27%, #f8764f 34%, #49c587 42%, #4bc9e2 49%, #ff7575 56%, #49c587 63%, #ff7575 70%, #f0c851 77%, #8d74b9 84%, #f8764f 91%, #49c587 98%, #49c587 100%) repeat scroll 0 0;
  height: 5px;
  margin-bottom: -1px;
  width: 100%;
}
.menu--top-toolbar li > a {
  color: $link-color-light !important;
  font-size: 12px !important;
}
.menu--top-toolbar li > a:focus {
  border-color: $link-color-lighter;
  box-shadow: 0 0 10px $link-color-lighter;
  color: $link-color-light;
  outline: none;
}
.menu--top-toolbar li > a:hover {
  color: $link-color-light;
}
.menu--top-toolbar li > a:visited {
  color: $link-color-light-darker;
}
.navbar-inverse .menu--main > li > a {
  color: #000;
  font-size: 16px;
  font-weight: 700;
  margin-right: 50px;
}
.navbar-inverse .menu--main > li > a:focus {
  border-color: $link-color-lighter;
  box-shadow: 0 0 10px $link-color-lighter;
  color: $link-color-light;
  outline: none;
}
.navbar-inverse .menu--main > li > a:hover {
  color: $link-color-light;
}
.navbar-inverse .menu--main > li > a:visited {
  color: $link-color-light-darker;
}
.menu--main.nav.navbar-nav a {
  position: relative;
}
.menu--main.nav.navbar-nav a:hover {
  color: #000;
}
.logo > img {
  margin: 5px;
  width: 180px;
}
#block-bootstrap-subtheme-main-menu {
  position: relative;
}
.navbar-collapse {
  width: 100%;
}
.logo-heading {
  display: inline;
}
.menu--top-toolbar {
  margin: 7px 0;
}
.nav > li > a {
  margin: 15px;
  padding: 0;
}
.nav > li > a:focus {
  background-color: transparent;
}
.navbar-inverse .navbar-nav > .open > a,
.navbar-inverse .navbar-nav > .open > a:hover,
.navbar-inverse .navbar-nav > .open > a:focus {
  background-color: transparent;
}
.menu--main.nav.navbar-nav {
  padding: 25px;
  vertical-align: middle;
}
.curve-up {
  background-color: #f9f9f9;
  clip-path: ellipse(90% 55% at 48% 55%);
  height: 150px;
  position: absolute;
  top: 200px;
  width: 100%;
  z-index: 1002;
}
@media screen and (max-width: 767px) {
  .curve-up {
    display: none;
  }
}
.mobile-adspace {
  display: none;
}

