/* Breadcrumbs */

.breadcrumb-container:after {
  clear: both;
  content: "";
  display: table;
}
.breadcrumb-container {
  background-color: $brand-primary;
  margin-bottom: 60px;
  padding: 0;
  width: 100%;
}
.breadcrumb {
  background-color: $brand-primary-darker;
  border-radius: 0;
  color: $white;
  display: inline;
  float: left;
  list-style: none;
  margin: 0;
  padding: 15px 25px 12px;
}
.breadcrumb .active,
.breadcrumb a,
.breadcrumb a:hover {
  color: $white;
}
.breadcrumb a:focus {
  border-color: #e9e9e9;
  box-shadow: 0 0 10px #e9e9e9;
  color: $link-color-light;
  outline: none;
}
.breadcrumb > li + li:before {
  color: $white;
}

/* Hover effect for breadcrumbs */

.breadcrumb a {
  display: inline-block;
  text-decoration: none;
}
.breadcrumb a::after {
  background-color: #ffffff;
  content: '';
  display: block;
  height: 3px;
  transition: width 0.3s;
  width: 0;
}
.breadcrumb a:hover::after {
  width: 100%;
}
