/**
 * Card styling
 */

.card {
  @include box-shadow;
  background: #fff;
  //border-radius: 2px;
  border-radius: 0;
  position: relative;
  z-index: 1;
  &:hover {
    .card__thumb {
      img {
          opacity: 0.6;
          transform: scale(1.1);
          -moz-transform: scale(1.1);
          -webkit-transform: scale(1.1);
      }
    }
  }
  .card__thumb {
    //border-radius: 2px 2px 0 0;
    border-radius: 0;
    background: #fff;
    //height: 175px;
    overflow: hidden;
    a::after {
      display: none;
      height: 0;
      // This removes the blank line under the image when a view is in a content region.
    }
    img {
      @include transitions;
      width: 100%;
    }
  }
  .card__content {
    background: #fff;
    border-radius: 2px;
    padding: 20px 15px;
    p {
      line-height: 1.5em;
    }
    .card__title {
      font-size: 1.2em;
      font-weight: 600;
      line-height: 1.2;
      margin: 0;
      padding: 0 0 15px;
      text-transform: uppercase;
      a:hover {
        text-decoration: none;
      }
    }
    .card__meta {
      color: $gray-light;
      .timestamp {
        margin: 0 16px 0 0;
      }
    }
  }
  .card__list {
    list-style: none;
    a {
      display: inline-block;
      padding: 10px 0;
      position: relative;
    }
  }
}
ul.card__date {
  color: #333;
  font-size: 12px;
}

/* Overrides for card styles */

.card.article_item {
  //min-height: 405px;
  //min-height: 421px;
}
.view-featured-video {
  // The featured video area has so much more space, it does not require the min. height.
  .card {
    .card__content {
      .card__title {
        margin-top: 25px;
        min-height: 0;
      }
    }
  }
}
.big_card.article_item .card__content {
  padding-top: 10px;
}
.district-calendar .card,
.district-events .card {
  margin-top: 25px;
}
.featured-video .card__content {
  margin-top: 10px;
}
.card {
  .card__date {
    margin-bottom: 10px;
  }
}
.card__description {
  min-height: 85px;
}
.department-listing {
  .card {
    margin-bottom: 25px;
    min-height: 315px;
    .card__content {
      padding: 15px;
    }
  }
}
.featured-links .card__title {
  min-height: 40px;
}
.school-listing {
  .card {
    margin-bottom: 25px;
    min-height: 427px;
    a::after {
      background: none;
    }
    a:hover::after {
      background: $brand-primary;
    }
    .card__content {
      padding: 15px;
    }
    .h2-layout {
      margin-bottom: 20px;
      margin-left: 15px;
    }
  }
  .col-lg-3 {
    padding-left: 5px !important;
    padding-right: 5px !important;
  }
}
.district-calendar .card,
.district-events .card {
  min-height: 460px;
}
#block-schoolboardandsuperintendent {
  .big_card {
    height: 250px;
    overflow: hidden;
    .card__thumb {
      height: 250px;
      overflow: hidden;
      img {
        height: 250px;
      }
    }
    .card__content {
      margin-left: -15px;
      margin-top: 10px;
    }
  }
}

/**
 * Breakpoints for card styles
 *
 * Tablet Styles < 1200px
 */

@media screen and (max-width: 1200px) {
  .card {
    margin-bottom: 25px;
    .card__content .card__title {
      font-size: 14px;
    }
    .card__thumb {
      //height: 150px;
    }
    .card__thumb a {
      //height: 150px;
      width: 100%;
    }
    .card__thumb a img {
      min-height: 150px;
    }
  }
}

/**
 * Phone Styles < 769px
 *
 * @TODO: Fix these styles
 */

@media screen and (max-width: 769px) {
  .district-calendar .card,
  .district-events .card {
    min-height: auto;
  }
  .big-card .card__content,
  .featured-video .card__content {
    margin: 0;
  }
  .card .card__content {
    padding: 15px;
  }
  .big_card.card {
    height: auto;
    margin: 10px;
  }
  .big_card .card__content {
    margin-left: 15px;
  }
  .big_card {
    overflow: hidden;
    height: auto;
  }
}
@media screen and (max-width: 996px) {
  #block-schoolboardandsuperintendent {
    .big_card {
      height: auto;
      overflow: hidden;
      .card__thumb {
        height: 300px;
        overflow: hidden;
        img {
          height: auto;
        }
      }
      .card__content {
        margin-left: 10px;
        margin-top: 10px;
      }
    }
  }
}
