/**
 * Pager pagination
 */

.pager {
  font-weight: 100;
  list-style: none;
  margin: 0;
  @include clearfix;
  li {
    > a {
      border-radius: 50%;
      color: #525252;
      font-weight: 100;
      height: 35px;
      line-height: 23px;
      margin: 5px;
      width: 35px;
    }
    > a:hover,
    > a:focus {
      background-color: $pager-hover-bg;
      text-decoration: none;
    }
  }
}
// Pager for mobile view in district hightlights section
.view-district-highlights-mobile- nav {
  position: absolute;
  width: 100%;
  z-index: 99999;
}
.featured-pages, .student-gallery, .block-views-blockprograms-block-1 {
  ul.pager.js-pager__items {
      margin-top: -55px;
      position: absolute;
      right: 5px;
  }
  .pager {
    font-weight: 100;
    list-style: none;
    margin: 0;
    text-align: right;
    @include clearfix;
    li {
      display: inline-block;
      > a {
        border-radius: 50%;
        color: #525252;
        display: inline-block;
        float: left;
        font-weight: 100;
        height: 35px;
        line-height: 23px;
        margin: 5px;
        // padding: 5px;
        width: 35px;
      }
      > a:hover,
      > a:focus {
        background-color: $pager-hover-bg;
        text-decoration: none;
      }
    }
    .previous {
      > a,
      > span {
        float: none; // Overriding default of left
      }
    }
    .next {
      > a,
      > span {
        float: none; // Overriding default of right
      }
    }
    .disabled {
      > a,
      > a:hover,
      > span,
      > a:focus {
        background-color: $pager-bg;
        color: $pager-disabled-color;
        cursor: $cursor-disabled;
      }
    }
  }
}
.student-gallery ul.pager.js-pager__items {
  right: -775px;
}
