/**
 * Default Variables.
 *
 * Modify this file to provide default Bootstrap Framework variables. The
 * Bootstrap Framework will not override any variables defined here because it
 * uses the `!default` flag which will only set their default if not already
 * defined here.
 *
 * You can copy existing variables directly from the following file:
 * ./libraries/bootstrap/assets/stylesheets/bootstrap/_variables.scss
 *
 * Scaffolding
 *
 * Settings for some of the most global styles.
 * Background color for `<body>`
 */

$body-bg: #f9f9f9;

// Gray and brand colors for use across Bootstrap

$brand-primary: #3ab9d9;
$brand-secondary: #222;
$brand-primary-darker: darken($brand-primary, 18);

$white: #fff;

.department-page {
  background-color: #fff;
}

/**
 * Typography
 *
 * Font, line-height, and color for body text, headings, and more
 */

@import url('https://fonts.googleapis.com/css?family=Open+Sans');
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,600,700,800,900');
$font-family-sans-serif: 'Open Sans', sans-serif;
$font-size-base: 14px;
$font-size-large: ceil(($font-size-base * 1.25)); // ~18px
$font-size-small: ceil(($font-size-base * 0.85)); // ~12px
$font-size-h1: 2em; // floor(($font-size-base * 1.5)); // ~36px
$font-size-h2: 1.4em; // floor(($font-size-base * 1)); // ~30px
$font-size-h3: 1.2em; //ceil(($font-size-base * 1)); // ~24px
$font-size-h4: 1em; // ceil(($font-size-base * 1)); // ~18px
$font-size-h5: $font-size-base;
$font-size-h6: ceil(($font-size-base * 0.85)); // ~12px
$link-color: #0974ad;
$link-color-lighter: lighten($link-color, 15);
$link-color-darker: darken($link-color, 15);
$link-color-light: #ffffff;
$link-color-light-darker: darken($link-color-light, 15);

/**
 * Buttons
 *
 * For each of Bootstrap's buttons, define text, background and border color
 */

$btn-font-weight: bold;
$btn-primary-bg: $brand-primary-darker;
$btn-primary-border: darken($btn-primary-bg, 0);

/**
 * Tables
 *
 */

$table-bg-accent: #f1f1f1 !default;
