/**
 * _landing.scss
 * Used for one-off styles that are not used globally.
 * For example: Homepage slideshow, homepage district hightlights, student gallery on the Students page, etc.
 *
 * Slideshow on homepage
 */

.slick {
  background: #000;
  border-bottom: 5px solid $brand-primary;
  clip-path: ellipse(120% 55% at 48% 55%);
  height: 80vh;
  margin-top: -115px;
  max-height: 600px;
  overflow: hidden;
  position: relative;
  z-index: 1003;
}
.slick-slider {
  max-height: 560px;
}
.slick-list {
  max-height: 595px;
}
.slick-slider-overlay {
  background-color: rgba(0, 0, 0, .4);
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.slick-prev::before {
  content: '\f104' !important;
  font-family: "Font Awesome 5 Free";
  font-family: "Font Awesome 5 Free";
  font-size: 30px;
  font-weight: 600;
}
.slick-next::before {
  content: '\f105' !important;
  font-family: "Font Awesome 5 Free";
  font-size: 30px;
  font-weight: 600;
}
.slide__caption {
  left: 15%;
  position: absolute;
  top: 15%;
  width: 70%;
  z-index: 999;
}
.slide__title {
  color: #fff;
  display: inline-block;
  font-family: "Montserrat";
  font-size: 2em;
  font-style: normal;
  font-weight: 800;
  letter-spacing: 1px;
  line-height: 30px;
  margin-bottom: 30px;
  padding: 0;
  text-transform: none !important;
  -webkit-animation-delay: 0.7s;
}
.slick-arrow::before {
  color: #fff;
  font-size: 3.25rem;
}
.slick-arrow {
  width: 75px;
}
.slick--skin--fullwidth .slide__constrained {
  margin-left: -35%;
  width: 1170px;
}
.slick--skin--fullwidth .slide__link a {
  border-top: 0;
}
.slick-dots li button:before {
  color: $white;
  opacity: .6;
}
.slick-dots li.slick-active button:before {
  color: $white;
  opacity: 1;
}

/* Tablet styles (< 1200px) for Slick Carousel */

@media screen and (max-width: 1200px) {
  .slick--skin--fullwidth .slide__constrained {
      margin-left: 5%;
      position: absolute;
  }
  .slick {
      clip-path: none;
      height: 40vh;
      margin-top: 0;
  }
}

/* Phone styles (< 767px) for Slick Carousel */

@media screen and (max-width: 767px) {
  .slick {
    clip-path: none;
    margin: 0;
    max-height: 250px;
    z-index: 1;
  }
  .slick--skin--fullwidth .slide__constrained {
    position: absolute;
    top: 0;
  }
  .slick--skin--fullwidth .slide__title {
    font-size: 1.1em;
  }
}

/**
 * District Highlights on home page
 *
 * Hide the mobile version of the view on desktop
 */

#block-views-block-district-highlights-mobile-block-1 {
  display: none;
}
#district_highlights {
  background-repeat: no-repeat;
  background-size: cover;
  background: url("https://s8.postimg.cc/5h7grj96t/jess-watters-483666-unsplash.jpg"); //@TODO: local
  color: #fff;
  padding: 80px;
  position: relative;
  z-index: 1;
}
#district_highlights::after {
  @include dark-overlay(rgba(0, 0, 0, 0.8));
  content: "";
  z-index: -1;
}
.region.region-district-highlights {
  margin-top: 100px;
}
.dh__row {
  &:nth-child(1) {
    display: block;
    font-family: "Montserrat";
    font-size: 2em;
    font-weight: 800;
  }
  &:nth-child(2) {
    display: block;
    font-family: "Open Sans";
    font-size: 1.5em;
    font-weight: bolder;
    text-transform: uppercase;
  }
}
.dh__col {
  border-right: 1px solid #d6d6d6;
  display: inline-block;
  z-index: 1;
  &:last-child {
    border-right: medium none;
  }
  &:nth-child(1) {
    color: #fa8c00;
  }
  &:nth-child(2) {
    color: #43a047;
  }
  &:nth-child(3) {
    color: #00acc2;
  }
  &:nth-child(4) {
    color: #B960F2;
  }
}

/**
 * Breakpoints for district hightlights section
 *
 * Tablet styles < 1200px
 */

@media screen and (max-width: 1200px) {
  .region.region-district-highlights {
    margin-top: 50px;
    position: relative;
  }
}

/* Phone styles < 767px */

@media screen and (max-width: 767px) {
  #district_highlights {
    padding: 40px;
    position: relative;
  }
  .dh__col {
    &:nth-child(1) {
      color: $link-color;
    }
  }
  #block-views-block-district-highlights-mobile-block-1 {
    display: block;
  }
  #block-views-block-district-highlights-block-1 {
    display: none;
  }
}

/**
 * Calendar and Events on home page
 *
 * Additional styles in _cards.scss
 */

.district-calendar h3,
.district-events h3 {
  margin-top: 0;
}
.district-calendar,
.district-events {
  margin-bottom: 35px;
}
.calendar__info ul,
.district-events ul {
