  font-size: 13px;
}
.event__left__col {
  height: 60px;
  overflow: hidden;
}
.calendar,
.events {
  background-color: #fff;
}
.calendar__item,
.event__item {
  margin: 5px -5px 15px;
}
.calendar__date {
  background-color: $link-color;
  padding: 5px;
}
.calendar__date,
.event__date {
  border-radius: 2px;
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
}
.event__date {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 5px;
  position: absolute;
  width: 80%;
}
.event__image .img-responsive {
  max-width: 103%;
}
.district-calendar h3 a,
.district-events h3 a {
  color: #000;
  font-size: 15px;
  font-weight: bolder;
  margin: 0;
  text-transform: uppercase;
}
@media screen and (max-width: 767px) {
  .calendar__date,
  .event__date {
    font-size: 14px;
  }
}

