/* Audience landing pages (Parents, Students, Community, District) */

.programs-section {
  background-color: #fff;
  padding: 80px 0;
}
.student-gallery {
  column-count: 3;
  column-gap: 15px;
  margin-top: 20px;
  overflow: hidden;
  -moz-column-count: 3;
  -moz-column-gap: 15px;
  -webkit-column-count: 3;
  -webkit-column-gap: 15px;
  .gallery__item {
    margin-bottom: 15px;
    overflow: hidden;
    position: relative;
    .gallery__overlay {
      -webkit-transition: all 0.8s;
      background-color: rgba(0, 0, 0, 0.4);
      height: 100%;
      left: 0;
      opacity: 0;
      position: absolute;
      top: 0;
      transition: all 0.8s;
      width: 100%;
    }
    .caption {
      -webkit-transition: all 0.4s;
      bottom: 50%;
      color: #fff;
      font-weight: bold;
      margin-left: 15px;
      margin: 0 auto;
      opacity: 0;
      position: absolute;
      transition: all 0.4s;
      z-index: 1;
    }
    .gallery__img {
      /* For Chrome & friends. */
      break-inside: avoid;
      height: auto;
      page-break-inside: avoid;
      transition: 500ms;
      width: 100%;
      /* For Firefox. */
      -webkit-column-break-inside: avoid;
      /* For standard browsers like IE. :-) */
    }
  }
}
.student-gallery .gallery__item:hover .gallery__img {
  transform: scale(1.1);
  -moz-transform: scale(1.1);
  -webkit-transform: scale(1.1);
}
.student-gallery .gallery__item:hover .caption {
  bottom: 50%;
  opacity: 1;
}
.gallery__item:hover .gallery__overlay {
  opacity: 1;
}
@media screen and (max-width: 1200px) {
  .student-gallery {
    column-count: 3;
    -moz-column-count: 3;
    -webkit-column-count: 3;
  }
}
@media screen and (max-width: 767px) {
  .student-gallery {
    column-count: 3;
    -moz-column-count: 3;
    -webkit-column-count: 3;
  }
  .student-gallery div {
    margin: 0;
    width: 200px;
  }
}
@media screen and (max-width: 650px) {
  .student-gallery {
    column-count: 2;
    -moz-column-count: 2;
    -webkit-column-count: 2;
  }
  .student-gallery div {
    margin: 0;
    width: 200px;
  }
}
